}",".widget {\n    background-color: $light-blue;\n    margin-bottom: $xl-size;\n}\n\n.widget-header {\n    background-color: $blue;\n    color: $off-white;\n    padding: $s-size;\n    display: flex;\n    justify-content: space-between;\n}\n\n.widget-header__title {\n    margin: 0;\n}\n\n.widget__message {\n    color: $off-white;\n    margin: 0;\n    padding: $m-size;\n    text-align: center;\n    color: lighten($off-white, 5%);\n    border-bottom: 1px solid lighten($light-blue, 10%);\n}",".option  {\n    display: flex;\n    justify-content: space-between;\n    padding: $s-size;\n    border-bottom: 1px solid lighten($light-blue, 10%);\n}\n\n.option__text {\n    color: whitesmoke;\n    font-weight: 500;\n    font-size: 1.4rem;\n    margin: 0 1rem 0 0;\n    word-break: break-all;\n}",".add-option-error {\n    color: $off-white;\n    font-style: italic;\n    margin: $m-size 0 0 0;\n    padding: 0 $s-size;\n}\n\n.add-option {\n    display: flex;\n    flex-direction: column;\n    padding: $s-size;\n}\n\n.add-option__input {\n    flex-grow: 1;\n    color: $off-white;\n    background-color: $dark-blue;\n    border: none;\n    border-bottom: .3rem solid darken($dark-blue, 10%);\n    padding: $s-size;\n    margin: 0 0 $s-size 0;\n}\n\n@media (min-width: $desktop-breakpoint) {\n    .add-option {\n        flex-direction: row;\n    }\n\n    .add-option__input {\n        margin: 0 $s-size 0 0;\n    }\n}",".ReactModalPortal > div {\n    opacity: 0;\n}\n\n.ReactModalPortal .ReactModal__Overlay {\n    transition: opacity .2s ease-in-out;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\n.ReactModalPortal .ReactModal__Overlay--after-open {\n    opacity: 1;\n}\n\n.ReactModalPortal .ReactModal__Overlay--before-open {\n    opacity: 0;\n}\n\n.modal {\n    color: $off-white;\n    background-color: $light-blue;\n    max-width: 30rem;\n    outline: none;\n    padding: $m-size;\n    text-align: center;\n}\n\n.modal__title {\n    margin: 0 0 $m-size 0;\n}\n\n.modal__body {\n    margin: 0 0 $m-size 0;\n    font-size: 1.5rem;\n    font-weight: 400;\n    color: white;\n    word-break: break-all;\n}"],"sourceRoot":""}]);
